.top{
    width: 100%;
    height: 100px;
   
    position: sticky;
    top: 0;
    align-items: center;
    display: flex;

    background-color: rgb(255, 221, 255);

    font-family: 'Josefin Sans',sans-serif;
    z-index: 999;

}

.topleft,.topright{
    flex:10;
    display: felx;
    align-items: center;
    justify-content: center;
  

}
.topcenter{
    flex: 7;
}


.topIcon{
    font-size: 30px;
    margin-right: 15px;
    color: rgb(7, 6, 6);
    cursor: pointer;
}

.topImg{
    width: 20px;
    height: 40px;
    border-radius: 50%;

    object-fit: cover;
}

.topList{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style:none;
}
.topListItem{
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;

}

.topsearchIcon{
    font-size: 18;
    color: #666;
    cursor: pointer;
    align-items: center;
    margin-left: 15px ;
       

}