.write{
    padding-top: 50px;

}

.writeform{
    position: relative;

}
.writeformgroup{
    margin-left: 150px;
    display: flex;
    align-items: center;
}
.writeicon{
    width: 65px;
    height: 25px ;
    border-radius: 50%;
    border: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: grey;
    cursor: pointer;
}
 
.writeInput{
    font-size: 38px;
    border: none;
    padding: 20px;
    width: 70vw;
}
.writeInput:focus{
    outline: none;

}
.writetext{
    
}
.writesubmit{
    position: absolute;
    top: 20px;
    right: 50px;
    color: white;
    background-color: teal;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;

}
.writeimg{
    width: 80%;
    height: 450px;
    margin-left: 150px;
    border-radius: 10px;
    object-fit: cover;
}
