.settings {
    display: flex;
    
  }
  
  .settingswrapper {
    flex: 10;
    padding: 20px;
  }
  
  .settingsTitle {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .settingsupdatetitle {
    font-size: 30px;
    margin-bottom: 20px;
    color: lightcoral;
  }
  
  .settingdeletetitle {
    color: red;
    font-size: 12px;
    cursor: pointer;
  }
  
  .settingform {
    display: flex;
    flex-direction: column;
  }
  
  .settingpp{
    display: flex;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .settingpp > img {
    height: 200px;
    width: 100px;
    border-radius: 20px;
    object-fit: cover;
  }
  
  .settingppicon {
    width: 25px;
    height: 25px;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 50%;
    margin-left: 10px;
    color: white;
    background-color: lightcoral;
    cursor: pointer;
  }
  
  .settingppbutton:focus {
    outline-style: none;
  }
  
  .settingform > label {
    font-size: 20px;
    margin-top: 20px;
  }
  
  .settingsInput {
    width: 30%;
  }
  
  .settingform > input {
    color: gray;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightgray;
  }
  
  .settingsubmit {
    align-self: center;
    width: 150px;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .settingsubmitbutton:hover {
    background-color: rgb(1, 114, 114);
  }