.singlePost{
    flex: 9;


}
.singlepostwrapper{
    padding: 20px;
    padding-right: 20px;
}
.singelpostimg{
    width: 100%;
    height: 500px;
    border-radius: 5px;
    object-fit: cover;
}

.singleposttitle{
    text-align: center;
    margin: 10px;
    font-family: 'Lora',serif;
    font-size: 20px;
}
.singlepostedit{
    float: right;
    font-size: 16px;

}
.singlepostIcon{
    margin-left: 10px;
    cursor: pointer;
    font-size: 25px;

}
.singlepostIcon:first-child{
    color: teal;

}
.singlepostIcon:last-child{
    color: tomato;
}
.singlepostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: rgb(36, 33, 211);
}
.singlepostDesc{
    color: #666;
    font-size: 18px;
    line-height: 25px;
}
.singlepostDesc::first-letter{
    margin-left: 20px;
    font-size: 60px;
    font-weight: 500px;
}


