.sidebar{
    flex: 2;
    margin: 20px;
    padding-bottom: 30px;
    background-color: white;
    border-radius:10px;
    display:10px;
    flex-direction:column;

}

.sidebarItem{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.sidebarTitle{
    margin: 10px;
    padding: spx;
    width: 80%;
      
    font-family: 'Varela Round',sans-serif;

    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.sidebarItem>img{

    margin-top: 15px;

}
.sidebarItem>p{
    padding: 80px;

}

.sidebarList{
    list-style: none;
    margin-bottom: 90px;

}
.sidebarListItem{
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}
.sidebarSocial{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;

}
.SidebarIcon{
    font-size: 16px;
    margin-left: 10px;
    cursor: pointer;
}



